.nav {
    position: absolute;
    z-index: 100;
    left: 0;
    right: 0;
    transition: 1s; }


.scrolled {
    border-radius: 0 0 20px 20px;
    -webkit-box-shadow: 0px 10px 22px -10px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 10px 22px -10px rgba(0,0,0,0.75);
    box-shadow: 0px 10px 22px -13px rgba(0,0,0,0.75);
    position: fixed;
    z-index: 100;
    padding-bottom: 30px;
    background: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
    // background: linear-gradient(0deg, rgba(5,13,29,0.5) 0%, rgba(5,13,29,0.5368522408963585) 100%);
    top: -80px;
    transform: translateY(100%);
    transition: 1s;
    .logo {
        padding-top: 35px; } }

.scrolledNav {
    position: fixed;
    z-index: 100;
    padding-bottom: 30px;
    background: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
    top: -80px;
    transition: 1s;
    .logo {
        padding-top: 35px; } }

.scrolledNav .nav-burger-wrapper {
    display: none; }

@media (max-width: $screen-md) {
    .scrolled {
        padding-bottom: 10px;
        top: -100px; }
    .logo {
        padding-top: 25px !important; }
    .scrolledNav {
        top: -120px; } }

.goTop {
    display: inline-block;
    position: fixed;
    right: -100px;
    bottom: 20px;
    z-index: 100;
    opacity: 0.6;
    transition: 0.3s;
    border-radius: 50%; }


.scrolledBtn:hover {
    cursor: pointer;
    opacity: 1; }


.scrolledBtn {
    transform: translateX(-120px); }
